<template>
  <div v-show="reg" class="back"><a href="#top">
    <div class="el-icon-arrow-up">
      
    </div>

  </a></div>
</template>

<script>
export default {
  data() {
    return {
      height: '',
      reg: false,
    }
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll(e) {
      this.height = e.target.documentElement.scrollTop || e.target.body.scrollTop; // 执行代码
      if (this.height >= 500) {
        this.reg = true;
      }
      if (this.height <= 500) {
        this.reg = false;
      }
    },
  }
}
</script>

