<template>
	<div>
		<div id="top"></div>
		<top :cate="cate" :images="images"></top>
		<art :cate="cate" :ServiceList="list"></art>
		<good v-on:fun="getdata"></good>
		<back></back>
		<stamp></stamp>
		<floor :title="cate.title"></floor>
	</div>
</template>

<script>
	import top from '@/components/detail_top.vue'
	import back from '@/components/back.vue'
	import art from '@/components/article.vue'
	import floor from '@/components/floor.vue'
	import {
		requirelist,
		codeServiceList
	} from "@/api/home";
	import good from "@/components/good";
	import stamp from '@/components/stamp'
	export default {
		name: "detail",
		components: {
			top,
			back,
			art,
			floor,
			good,
			stamp,
			
		},
		data() {
			return {
				page: [],
				cate: [],
				strs: '',
				images: [],
				list:[]
			}
		},
		mounted() {
			let data = this.$route.query;
			this.page = {
				"product_id": data.id,
			};
			requirelist(this.page).then(res => {

				this.cate = res;
				this.list=this.cate.service;
				
				var str = this.cate.images; //这是一字符串
				var strs = new Array(); //定义一数组
				strs = str.split("|"); //字符分割
				for (var i = 0; i < strs.length; i++) {
					this.images.push(strs[i]); //分割后的字符输出
				}
				// console.log(this.images);
			}).catch(error => {
				console.log(error);
			})
		},
		methods: {
			getdata(res) {
				this.page = {
					"product_id": res.id,
				};
				requirelist(this.page).then(res => {

          this.cate = res;
          // console.log(this.cate);
		  this.list=this.cate.service;
          var str = this.cate.images; //这是一字符串
          var strs = new Array(); //定义一数组
          strs = str.split("|"); //字符分割
          for (var i = 0; i < strs.length; i++) {
            this.images.push(strs[i]); //分割后的字符输出
          }
          // console.log(this.images);
        }).catch(error => {
					console.log(error);
				})
			}
		}
	}
</script>

